import { List1, List2 } from "./StaticVariables";
import ToolbarIcon from "./ToolbarIcon";
export function Toolbar2() {
  const lst = List2();
  return (
    <>
      {lst.map((d) => (
        <ToolbarIcon
          Title={d.Title}
          DataCommand={d.DataCommand}
          IconArr={d.Icon}
          key={d.DataCommand}
        ></ToolbarIcon>
      ))}
    </>
  );
}
export function Toolbar1() {
  const lst1 = List1();
  return (
    <>
      {lst1.map((d) => (
        <ToolbarIcon
          Title={d.Title}
          DataCommand={d.DataCommand}
          IconArr={d.Icon}
          key={d.DataCommand}
        ></ToolbarIcon>
      ))}
    </>
  );
}

export default Toolbar1;
