import NavigationItem from "./NavigationItem";
import React, { useContext, useEffect } from "react";
import { UserContext } from "../Contexts/UserContext";

// import "./css/Navigation.css";
const itemlist = [
  ["Inbox", "fa fa-inbox"],
  ["Outbox", "fas fa-upload"],
  ["Sent", "far fa-paper-plane"],
  ["Draft", "far fa-edit"],
  ["Trash", "fa fa-trash"],
];
function Navigation() {
  const ctx = useContext(UserContext);
  useEffect(() => {
    console.log(ctx.mailbox);
    fetch(ctx.mailbox)
      .then((res) => res.json())
      .then((data) => {
        ctx.setData(data.time);
        console.log(data.time);
      });
  }, [ctx.mailbox]);
  function ReadMessages(M) {
    console.log("Read");
    ctx.setmailbox(M);
    ctx.setIsCompose(false);
  }
  return (
    <nav id="sidebar" className="sidenavbar">
      <div
        className="img bg-wrap text-center py-4"
        style={{ backgroundImage: "url(images/bg_1.jpg)" }}
      >
        <div className="user-logo">
          <div
            className="img"
            style={{ backgroundImage: "url(images/logo.jpg)" }}
          ></div>
          <h3>Raghav Verma</h3>
        </div>
      </div>
      <ul className="list-unstyled components mb-5">
        <li className="composeButton" onClick={() => ctx.setIsCompose(true)}>
          <a href="#">
            <i className="fa fa-plus"></i> Compose
          </a>
        </li>
        {itemlist.map((d) => (
          <NavigationItem
            mailboxName={d[0]}
            icon={d[1]}
            key={d[0]}
            onClick={() => ReadMessages(d[0])}
          ></NavigationItem>
        ))}
      </ul>
    </nav>
  );
}
export default Navigation;
