import FontPallete from "./FontPallete";
export function ForeGroundPalette() {
  return (
    <div className="fore-wrapper">
      <i
        className="fa fa-font"
        data-toggle="tooltip"
        data-placement="top"
        title="text color"
        style={{ color: "#C96" }}
      ></i>
      <FontPallete cls="fore-palette" DataCommand="foreColor"></FontPallete>
    </div>
  );
}
// export default ForeGroundPalette;
export function BackGroundPalette() {
  return (
    <div className="back-wrapper">
      <i
        className="fa fa-font"
        data-toggle="tooltip"
        data-placement="top"
        title="Background Color"
        style={{ color: "#C96" }}
      ></i>
      <FontPallete cls="back-palette" DataCommand="backColor"></FontPallete>
    </div>
  );
}
// export default BackgroundPalette;
