import $ from "jquery";
import { ColorList } from "./StaticVariables";
import { changeFormatting } from "./Utility";

function FontPallete({ cls, DataCommand }) {
  const colorList = ColorList();
  return (
    <div className={cls}>
      {colorList.map((color) => (
        <a
          href="#"
          data-command={DataCommand}
          data-value={`#${color}`}
          style={{ backgroundColor: `#${color}` }}
          className="palette-item"
          key={color}
          onClick={changeFormatting}
        ></a>
      ))}
    </div>
  );
}
export default FontPallete;
