// import Icon from "./Icon";
import { changeFormatting } from "./Utility";
function ToolbarIcon({ DataCommand, Title, IconArr }) {
  return (
    <a
      href="#"
      data-command={DataCommand}
      data-toggle="tooltip"
      data-placement="top"
      title={Title}
      onClick={changeFormatting}
    >
      {IconArr[0] ? <i className={IconArr[1]}></i> : IconArr[1]}
    </a>
  );
}
export default ToolbarIcon;
