import React, { useEffect, useContext } from "react";
import { UserContext } from "../Contexts/UserContext";
import DOMPurify from "dompurify";
import "./css/messagecontent.css";

function MessageContent() {
  const ctx = useContext(UserContext);
  useEffect(() => {
    if (ctx.MID == null) {
      ctx.setMessage("No Conversation Selected");
    } else {
      fetch(`messages/${ctx.MID}`)
        .then((res) => res.json())
        .then((msg) => {
          const myHTML = msg.data.message;
          console.log(myHTML);
          // const mySafeHTML = DOMPurify.sanitize(myHTML, {
          //   ADD_TAGS: ["img"],
          //   ADD_ATTR: ["src"],
          // });
          // console.log(mySafeHTML);
          ctx.setMessage(myHTML);
          ctx.setHeaders(msg.data.headers);
          // console.log(mySafeHTML);
          // console.log(ctx.Headers);
        });
    }
  }, [ctx.MID]);
  return (
    <div className="col-md-8 col-lg-8 col-xl-8 msgcontent">
      <div className="subjectdiv">{ctx.Headers.Subject}</div>
      <div className="fromdiv">{ctx.Headers.From}</div>
      <div className="datediv">{ctx.Headers.Date}</div>

      <div dangerouslySetInnerHTML={{ __html: ctx.message }} />
    </div>
  );
}
export default MessageContent;
