import ToolBar from "./ToolBar";
import "./css/style.css";
import { getHtmlContent, kpress } from "./Utility";
import "./css/Editor.css";
import { useContext, useEffect } from "react";
// import react from "react";
import { UserContext } from "../../Contexts/UserContext";

function Editor() {
  useEffect(() => {
    const editor = document.getElementById("editor");
    console.log(editor);
    editor.addEventListener("keypress", kpress);
  });
  const ctx = useContext(UserContext);
  function sendMessage(event) {
    event.preventDefault();
    const body = getHtmlContent();
    const message = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    };
    const response = fetch("/send", message);
    console.log(response.ok);
  }
  return (
    <div className="col-lg-12 editorwrapper">
      <div className="row w1">
        <div className="col-md-11 editorwrapper11 ">
          <div className="editor" id="editor-1">
            <ToolBar></ToolBar>
            <div className="compose">
              <input
                className="Fromi"
                type="email"
                value={ctx.UserName}
                readOnly
                placeholder="From"
              ></input>
              <input className="Toi" type="email" placeholder="To"></input>
              <input
                className="Subjecti"
                type="text"
                placeholder="Subject"
              ></input>
            </div>
            <div id="editor" className="editorAria" contentEditable></div>
          </div>
        </div>
      </div>
      <div className="row align-items-center justify-content-center w2">
        <div className="col-md-11 text-right my-3">
          <button
            className="btn btn-info"
            id="getHTML"
            get-data="editor-1"
            onClick={sendMessage}
          >
            SEND
          </button>
        </div>
      </div>
    </div>
  );
}
export default Editor;
